import React, { useState } from "react";
import Header from "./Header";
const FormData = () => {
  const [inputField, setInputField] = useState({
    id_number:" ",
    first_name: "",
    last_name: "",
    gmail: "",
  });
  const inputsHandler = (e) => {
    const { name, value } = e.target;
    setInputField((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const submitButton = (e) => {
    e.preventDefault();
    alert("Form Submit");
  };
  return (
    <div>
      <Header/>
      <div>
        <div className="container col-10 col-md-8 col-lg-7 ">
          <br />
          <form>
            <h1 className="col-md-8">Form Data</h1>
            <div className="form-row">
            <div className="form-row">
                <div className="form-group col-md-8">
                  <label for="inputNumber">Id Number</label>
                  <input
                    type="number"
                    className="form-control"
                    name="id_number"
                    onChange={inputsHandler}
                    placeholder="Number"
                    value={inputField.id_number}
                  ></input>
                </div>
              </div>
              <div className="form-row">
                <div className="form-group col-md-8">
                  <label for="inputName">First Name</label>
                  <input
                    type="txt"
                    className="form-control"
                    name="first_name"
                    onChange={inputsHandler}
                    placeholder="First Name"
                    value={inputField.first_name}
                  ></input>
                </div>
              </div>
              <div className="form-group col-md-8">
                <label for="inputName">Last Name</label>
                <input
                  type="txt"
                  className="form-control"
                  name="last_name"
                  onChange={inputsHandler}
                  placeholder="Lirst Name"
                  value={inputField.last_name}
                ></input>
              </div>
              <div className="form-group col-md-8">
                <label for="inputeEmail">Gmail</label>
                <input
                  type="gmail"
                  className="form-control"
                  name="gmail"
                  onChange={inputsHandler}
                  placeholder="Gmail"
                  value={inputField.gmail}
                />
              </div>
              <br />
              <button onClick={submitButton} class="btn btn-primary col-md-2">
                Submit
              </button>
            </div>
            {
        <div><br/>
          <h1>You are Submit This Info:</h1>
          <ul>
            {Object.entries(inputField).map(([name, value]) => (
              <li><h3 key={""}><strong>{""}</strong>{value.toString()}</h3>
              </li>
            ))}
          </ul>
        </div>
      }
          </form>
        </div>
      </div>
    </div>
  );
};

export default FormData;
